/**
 * TFLite Module - Public API
 * 
 * Centralized exports for TFLite inference functionality.
 * Use this for clean imports throughout the application.
 * 
 * @module lib/tflite
 */

// Main inference functions
export {
  runInference,
  runInferenceOnImageData,
  runBatchInference,
  warmupModel,
  type InferenceResult,
  type InferenceOptions
} from './runInference'

// Model management
export {
  loadModel,
  getModelInstance,
  isModelLoaded,
  clearModelCache
} from './loadModel'

// Preprocessing utilities
export {
  preprocessImage,
  preprocessFromDataUrl,
  preprocessFromImageBitmap,
  dataUrlToImageData,
  imageBitmapToImageData,
  getTensorStats,
  type PreprocessConfig,
  DEFAULT_INPUT_SIZE,
  DEFAULT_CHANNELS
} from './preprocess'
