/**
 * TFLite Model Loader
 * 
 * Handles loading and caching of the TFLite model using WebAssembly backend.
 * Ensures browser-only execution (no SSR).
 * 
 * @module lib/tflite/loadModel
 */

import * as tflite from '@tensorflow/tfjs-tflite'

// Model configuration
const MODEL_PATH = '/models/palm-oil-model.tflite'

// Singleton pattern for model caching
let modelInstance: tflite.TFLiteModel | null = null
let isLoading = false
let loadPromise: Promise<tflite.TFLiteModel> | null = null

/**
 * Initialize TFLite WebAssembly runtime
 * Must be called before loading the model
 */
async function initializeTFLite(): Promise<void> {
  if (typeof window === 'undefined') {
    throw new Error('TFLite can only be initialized in browser context')
  }

  try {
    // Set WASM path to the models directory
    tflite.setWasmPath('/models/')
    console.log('[TFLite] WebAssembly runtime initialized')
  } catch (error) {
    console.error('[TFLite] Failed to initialize WASM runtime:', error)
    throw error
  }
}

/**
 * Load TFLite model with caching
 * 
 * This function:
 * - Returns cached model if already loaded
 * - Prevents duplicate loading requests
 * - Initializes WASM runtime if needed
 * 
 * @returns Promise resolving to loaded TFLite model
 * @throws Error if model fails to load or not in browser context
 */
export async function loadModel(): Promise<tflite.TFLiteModel> {
  // Browser-only guard
  if (typeof window === 'undefined') {
    throw new Error('Model loading can only occur in browser context')
  }

  // Return cached model
  if (modelInstance) {
    console.log('[TFLite] Using cached model instance')
    return modelInstance
  }

  // Return in-progress load promise
  if (isLoading && loadPromise) {
    console.log('[TFLite] Model loading in progress, waiting...')
    return loadPromise
  }

  // Start new load
  isLoading = true
  loadPromise = (async () => {
    try {
      console.log('[TFLite] Initializing runtime...')
      await initializeTFLite()

      console.log('[TFLite] Loading model from:', MODEL_PATH)
      const startTime = performance.now()

      const model = await tflite.loadTFLiteModel(MODEL_PATH)

      const loadTime = (performance.now() - startTime).toFixed(0)
      console.log(`[TFLite] Model loaded successfully in ${loadTime}ms`)
      
      // Log model details
      console.log('[TFLite] Model inputs:', model.inputs)
      console.log('[TFLite] Model outputs:', model.outputs)

      modelInstance = model
      return model
    } catch (error) {
      console.error('[TFLite] Model loading failed:', error)
      modelInstance = null
      throw new Error(`Failed to load TFLite model: ${error instanceof Error ? error.message : 'Unknown error'}`)
    } finally {
      isLoading = false
      loadPromise = null
    }
  })()

  return loadPromise
}

/**
 * Get cached model instance without loading
 * Useful for checking if model is already loaded
 * 
 * @returns Model instance or null if not loaded
 */
export function getModelInstance(): tflite.TFLiteModel | null {
  return modelInstance
}

/**
 * Check if model is currently loaded
 */
export function isModelLoaded(): boolean {
  return modelInstance !== null
}

/**
 * Clear model cache
 * Call this if you need to force reload the model
 */
export function clearModelCache(): void {
  modelInstance = null
  console.log('[TFLite] Model cache cleared')
}
